import * as React from "react";
import {At, Key, Atom} from 'phosphor-react'
import {Box, TextField, Button, FormControl} from '@mui/material';
import AnMagicButtonComponent from "../../ui/an-buttons/an-magic-button/AnMagicButton.component";
import LogoComponent from "../Logo/logo.component";
import LoginService from "../../sevices/login.service";
import SessionService from "../../sevices/session.service";

interface IProps {
    submit?: any;
}

interface IState{
    email: string;
    password: string;
}

export default class InputFieldComponent extends React.Component<IProps, IState>{
    private Form: React.RefObject<HTMLInputElement>;

    public formData = new FormData()
    constructor(props: IProps) {
        super(props);
        this.Form = React.createRef();
        this.state = {email: '', password: ''}
        this.onChange = this.onChange.bind(this)
        this.submitForm = this.submitForm.bind(this)
    }

    onChange = (e: React.ChangeEvent<HTMLInputElement>): void => {

        e.preventDefault()
        switch (e.currentTarget.name){
            case 'email': {
                this.setState({email: e.currentTarget.value})
                break;
            }
            case 'password': {
                this.setState({password: e.currentTarget.value})
                break;
            }
        }
        console.log(e.currentTarget.name)

    };

    submitForm = (event: React.MouseEvent<HTMLButtonElement>) => {
        // Preventing the page from reloading
        event.preventDefault();
        this.formData.append('email', this.state.email)
        this.formData.append('password', this.state.password)
        console.log(this.formData)

        // Do something
        LoginService.login(this.formData).then((res)=>{
            console.log(res.data.data)
            SessionService.setCurrentUser(res.data.data?.user)
            SessionService.setToken(res.data.data?.user)
            return document.location.replace(document.location+'/menu')
        }).catch((e: Error)=> {
            console.log(e)
        })
    }

    render() {
        return (
            <Box sx={{ '& > :not(style)': { m: 2 } }}>
                <LogoComponent/>
                <Box sx={{ display: 'flex', alignItems: 'center', alignContent: 'center'}}>
                    <At size={40}/>
                    <TextField id="input-with-sx" label="E-Mail" variant="standard" name={'email'}
                    onChange={this.onChange} inputProps={{pattern: '[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$'}}/>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', alignContent: 'center'}}>
                    <Key size={40}/>
                    <TextField id="input-with-sx" label="Password" type={'password'} variant="standard" name={'password'}
                               onChange={this.onChange} inputProps={{pattern: '.{4,}'}}/>
                </Box>
                <Button variant="outlined" onClick={this.submitForm}>Log In</Button>
            </Box>
        )
    }
}
